.friend_list {
  display: inline-flex;
  flex-direction: column;
}

.friend_list_item {
  display: flex;
  box-shadow: 0px 4px 12px grey;
  padding: 8px 12px;
}

.friend_list_item:not(:last-child) {
  margin-bottom: 14px;
}

.status {
  border-radius: 50%;
}

.avatar {
  margin-right: 12px;
}
